<template>
<div >
   <mu-appbar class="fixed" title="">
     <mu-icon-button icon="arrow_back" slot="left" @click="goback()" />
   </mu-appbar>
<div style="padding-top:66px;">
 <mu-card>
   <mu-card-media >
    <div class="player">
          <video-player  class="vjs-custom-skin"
                         ref="videoPlayer"
                         :options="playerOptions"
                         :playsinline="true">
                       
          </video-player>
    </div>
   
  
  </mu-card-media>
 </mu-card>
 <mu-tabs :value="currentTab" @change="handleTabChange">
    <mu-tab value="tab1" title=""/>
    <mu-tab value="tab2" title=""/>
    <mu-tab value="tab3" title=""/>
  </mu-tabs>
  <div class="desc-container" v-if="currentTab === 'tab1'">
     
  </div>
  <div v-if="currentTab === 'tab2'"> 
  
  </div>
  <div v-if="currentTab === 'tab3'">
    <h2></h2>
    <p>
      
    </p>
  </div>
</div>
<mu-dialog :open="dialog" title="">
    <h3></h3>
    <mu-flat-button  label="" slot="actions" primary @click="()=>{dialog=false}"/>
  </mu-dialog>
</div>
</template>
<script >
  require('videojs-contrib-hls/dist/videojs-contrib-hls')

	export default {
		name:'livevideo',

    mounted(){

        this.setplayer()
    },
		
		data(){
			return {
				currentTab:'tab1',
        dialog:true,
        videoplayerurl:'',
       
        playerOptions:null
			}
		},
   
    beforeRouteEnter(to,from,next)
    {
      next(vm=>{
        vm.setplayer()
      })
    },

    beforeRouteLeave(to,from,next)
    {
       this.playerOptions=null;
       next(true)
     

    },
		computed:{
       videoObject() {
        return this.$store.state.liveobj
       }
    },
		methods: {
     
      setplayer(){
           this.$http.get(this.$store.state.serverUrl+"/live/?name="+this.videoObject.alt).then(response=>{
            
            var slist=[]
            slist.push({type:"application/x-mpegURL",src:response.body,withCredentials: false})
           
            this.playerOptions={
              aspectRatio:'16:9',
              sources:slist,
              controlBar: {
              timeDivider: false,
              durationDisplay: false
          },       
            }
            //alert(slist[0].src)
           
          },response=>{
        
          })

      },
     
			fetchData () {
				alert('hello')
			},
      handleTabChange (val) {
        this.currentTab = val
      },
      goback()
      {
        this.$router.go(-1)
      }
     

    } 
  }
</script>
<style scoped>
  
   .flex{
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
   }
   
   .desc-container{
    height:100%;
    overflow-y: scroll;
   }
   img {
    width: 100%;
   }
   .fixed {
    position:fixed;
    top: 0;
   }
 
</style>


